import axios from "axios";
import React from "react";
import { useParams, useNavigate } from "react-router-dom";



const FullPizza: React.FC = ({ }) => {


   const { id } = useParams();
   const navigate = useNavigate();



   const [pizza, setPizza] = React.useState<{
      imageUrl: string,
      title: string,
      price: number,
   }>();


   React.useEffect(() => {

      async function fetchPizza() {
         try {
            const { data } = await axios.get(`https://633c284b74afaef16403361d.mockapi.io/items/${id}`);
            setPizza(data);
         } catch (err) {
            console.log(err);
            alert('такая пицца не нашлась');
            navigate('/');
         }
      }

      fetchPizza()





   }, [])


   if (pizza === undefined) {
      return <>'загрузка...'</>
   }

   return (



      < div >
         <img src={pizza.imageUrl}></img>
         <h1>{pizza.title}</h1>
         <p>Lorem ipsum dolor, sit amet consectetur adipisicing.</p>
         <h4>{pizza.price}</h4>
      </ div >


   );
}

export default FullPizza;